<template>
    <el-dialog class="add-dialog" :title="title" :visible.sync="dialogVisible" fullscreen @close="resetForm">
        <el-tabs v-model="activeName">
            <el-tab-pane label="基本信息" name="first">
                <el-form ref="dialogForm" :model="dialogForm" label-width="150px" :rules="rules">
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="统一社会信用代码" prop="socialCode">
                                <el-input v-model="dialogForm.socialCode" placeholder="请填写统一社会信用代码" maxlength="18"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="登记证号" prop="registrationCode">
                                <el-input v-model="dialogForm.registrationCode" placeholder="请填写登记证号" maxlength="32"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="社会组织名称" prop="corportionName">
                                <el-input v-model="dialogForm.corportionName" placeholder="请填写社会组织名称" maxlength="100"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="登记机关代码" prop="manageOrganCode">
                                <el-input v-model="dialogForm.manageOrganCode" placeholder="请填写登记机关代码" maxlength="6"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="法定代表人姓名" prop="legalName">
                                <el-input v-model="dialogForm.legalName" placeholder="请填写法定代表人姓名" maxlength="80"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="住所" prop="residence">
                                <el-input v-model="dialogForm.residence" placeholder="请填写住所" maxlength="200"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="批准日期" prop="approvalDate">
                                <el-date-picker v-model="dialogForm.approvalDate" type="date" placeholder="请选择批准日期"></el-date-picker>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="社会组织类型" prop="socialType">
                                <el-select placeholder="请选择社会组织类型" v-model="dialogForm.socialType">
                                    <el-option v-for="(item, index) in dic['社会组织类型']" :key="index" :label="item.name"
                                        :value="item.code"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="状态">
                                <el-radio-group v-model="dialogForm.status">
                                    <el-radio :label="1">正常</el-radio>
                                    <el-radio :label="0">注销</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="负责人证件类型" prop="leaderIdcode">
                                <el-select v-model="dialogForm.leaderIdcode" placeholder="请选择负责人证件类型">
                                    <el-option v-for="(item, index) in dic['证件代码']" :key="index" :label="item.name"
                                        :value="item.code"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="负责人证件号码" prop="leaderCardnumber">
                                <el-input v-model="dialogForm.leaderCardnumber" placeholder="请填写负责人证件号码" maxlength="50"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="负责人姓名" prop="leaderName">
                                <el-input v-model="dialogForm.leaderName" placeholder="请填写负责人姓名" maxlength="80"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="负责人联系方式" prop="leaderContact">
                                <el-input v-model="dialogForm.leaderContact" placeholder="请填写负责人联系方式" maxlength="50"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="治保负责人姓名" prop="secuLeaderName">
                                <el-input v-model="dialogForm.secuLeaderName" placeholder="请填写治保负责人姓名" maxlength="50"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="治保负责人联系方式" prop="secuLeaderContact">
                                <el-input v-model="dialogForm.secuLeaderContact" placeholder="请填写治保负责人联系方式" maxlength="50"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item prop="houseId">
                                <el-popover placement="right" width="400" trigger="click" v-model="showPopover">
                                    <techo-house ref="vCheckedHouse" :isShowHouse="true" :butName="'确定'" :disCodeOption="disCodeOption"
                                        :buildOptions="buildOptions" :houseOptions="houseOptions" @onRelaData="handleCheckedHouse"
                                        @onQueryBuild="handleQueryBuildList" @onQueryHouse="handleQueryHouseList"></techo-house>
                                    <el-button class="check-house" slot="reference" type="success" size="small">请选择住所户</el-button>
                                </el-popover>
                            </el-form-item>
                        </el-col>
                        <el-col :span="16">
                            <el-form-item label="办公地址" prop="officeAddress">
                                <el-input v-model="dialogForm.officeAddress" placeholder="请填写办公地址" maxlength="200"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="关注程度" prop="concernedDegree">
                                <el-select placeholder="请选择关注程度" v-model="dialogForm.concernedDegree">
                                    <el-option v-for="(item, index) in dic['关注程度']" :key="index" :label="item.name"
                                        :value="item.code"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            
                        </el-col>
                        
                        <el-col :span="8">

                        </el-col>
                    </el-row>
                </el-form>
            </el-tab-pane>
            <el-tab-pane label="附加信息" name="second">
                <el-form :model="dialogForm" label-width="150px" :rules="rules">
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="是否具备建立中共党组织条件" label-width="200px">
                                <el-radio-group v-model="dialogForm.isHaveConditions">
                                    <el-radio :label="1">是</el-radio>
                                    <el-radio :label="0">否</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="是否有海外背景">
                                <el-radio-group v-model="dialogForm.isHaveAbroad">
                                    <el-radio :label="1">是</el-radio>
                                    <el-radio :label="0">否</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="资金来源">
                                <el-input v-model="dialogForm.sourcesFunds" placeholder="请填写资金来源" maxlength="100"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="是否有中共党组织" label-width="200px">
                                <el-radio-group v-model="dialogForm.isHaveCommunist">
                                    <el-radio :label="1">是</el-radio>
                                    <el-radio :label="0">否</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="中共党员数量" prop="communistCount">
                                <el-input v-model="dialogForm.communistCount" placeholder="请填写中共党员数量" maxlength="6"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">

                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="是否有工会" label-width="200px">
                                <el-radio-group v-model="dialogForm.isHaveUnion">
                                    <el-radio :label="1">是</el-radio>
                                    <el-radio :label="0">否</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="工会会员数量" prop="unionCount">
                                <el-input v-model="dialogForm.unionCount" placeholder="请填写工会会员数量" maxlength="6"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">

                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="是否有共青团组织" label-width="200px">
                                <el-radio-group v-model="dialogForm.isHaveYleague">
                                    <el-radio :label="1">是</el-radio>
                                    <el-radio :label="0">否</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="共青团员数量" prop="yleagueCount">
                                <el-input v-model="dialogForm.yleagueCount" placeholder="请填写共青团员数量" maxlength="6"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form>

                            </el-form>
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="是否有妇联组织" label-width="200px">
                                <el-radio-group v-model="dialogForm.isHaveWomensorg">
                                    <el-radio :label="1">是</el-radio>
                                    <el-radio :label="0">否</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="妇女数量" prop="womenCount">
                                <el-input v-model="dialogForm.womenCount" placeholder="请填写妇女数量" maxlength="6"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">

                        </el-col>
                    </el-row>
                </el-form>
            </el-tab-pane>
        </el-tabs>
        <div slot="footer" class="dialog-footer">
            <el-button v-if="activeName == 'first'" type="primary" size="small" @click="activeName = 'second'">下一步</el-button>
            <el-button v-if="activeName == 'second'" type="primary" size="small" @click="activeName = 'first'">上一步</el-button>
            <el-button v-if="activeName == 'second'" type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button v-if="activeName == 'second'" type="success" size="small" @click="submitForm">保存</el-button>
        </div>
    </el-dialog>
</template>

<script>
import service from "./services/double_origanization_service.js";
export default {
    props: {
    disCodeOption: {
      type: Array,
      default: []
    }
  },
  data() {
    return {
      title: "",
      dialogVisible: false,
      isAdd: false,
      activeName: "first",
      buildOptions: [],
      houseOptions: [],
      showPopover: false,
      dic: {
        社会组织类型: [],
        关注程度: [],
        证件代码: []
      },
      dialogForm: {
        status: 1,
        isHaveConditions: 0,
        isHaveCommunist: 0,
        isHaveUnion: 0,
        isHaveYleague: 0,
        isHaveWomensorg: 0,
        isHaveAbroad: 0,
        houseId: "",
        districtCode: "",
        officeAddress: "",
        leaderIdcode: "111"
      },
      rules: {
        socialCode: [
          {
            required: true,
            message: "请填写统一社会信用代码",
            trigger: "blur"
          },
          {
            min: 18,
            max: 18,
            message: "长度为18位",
            trigger: "blur"
          }
        ],
        registrationCode: [
          {
            required: true,
            message: "请填写登记证号",
            trigger: "blur"
          }
        ],
        corportionName: [
          {
            required: true,
            message: "请填写社会组织名称",
            trigger: "blur"
          }
        ],
        manageOrganCode: [
          // {
          //   required: true,
          //   message: "请填写管理机关代码",
          //   trigger: "blur"
          // },
          {
            min: 6,
            max: 6,
            message: "长度为6位",
            trigger: "blur"
          }
        ],
        legalName: [
          {
            required: true,
            message: "请填写法定代表人姓名",
            trigger: "blur"
          }
        ],
        residence: [
          {
            required: true,
            message: "请填写住所",
            trigger: "blur"
          }
        ],
        approvalDate: [
          // {
          //   required: true,
          //   message: "请选择批准日期",
          //   trigger: "change"
          // }
        ],
        socialType: [
          {
            required: true,
            message: "请选择社会组织类型",
            trigger: "change"
          }
        ],
        leaderIdcode: [
          {
            required: true,
            message: "请选择负责人证件类型",
            trigger: "change"
          }
        ],
        leaderCardnumber: [
          {
            required: true,
            message: "请填写负责人证件号码",
            trigger: "blur"
          }
        ],
        leaderName: [
          {
            required: true,
            message: "请填写负责人姓名",
            trigger: "blur"
          }
        ],
        leaderContact: [
          {
            required: true,
            message: "请填写负责人联系方式",
            trigger: "blur"
          },
          util.validate.tellAndPhone()
        ],
        officeAddress: [
          {
            required: true,
            message: "请填写办公地址",
            trigger: "blur"
          }
        ],
        // secuLeaderName: [
        //   {
        //     required: true,
        //     message: "请填写治保负责人姓名",
        //     trigger: "blur"
        //   }
        // ],
        secuLeaderContact: [
          // {
          //   required: true,
          //   message: "请填写治保负责人联系方式",
          //   trigger: "blur"
          // },
          util.validate.tellAndPhone()
        ],
        // concernedDegree: [
        //   {
        //     required: true,
        //     message: "请选择关注程度",
        //     trigger: "change"
        //   }
        // ],
        communistCount: [util.validate.valiInteger()],
        unionCount: [util.validate.valiInteger()],
        yleagueCount: [util.validate.valiInteger()],
        womenCount: [util.validate.valiInteger()],
        houseId: [
          { required: true, message: "请选择现住地户", trigger: "change" }
        ]
      }
    };
  },
  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            that.dic[item.name] = data;
          });
        }
      });
    });
  },
  watch: {
    activeName(name) {
      if (name == "second") {
        this.$refs["dialogForm"].validate(val => {
          if (!val) {
            this.$nextTick(() => {
              this.activeName = "first";
            });
          }
        });
      }
    }
  },
  methods: {
      /** 选择现住地户 */
    handleCheckedHouse(value, buildInfo) {
      this.dialogForm.districtCode = buildInfo.districtCode;
      let houseInfo = _.find(this.houseOptions, item => {
        return item.id == value.houseId;
      });
      service.requestCodeByAddress(buildInfo.districtCode, data => {
        this.dialogForm.officeAddress =
          "陕西省" + data.belongname + data.name + houseInfo.name;
        this.showPopover = false;
      });
      this.dialogForm.houseId = houseInfo.id;
      this.$refs["dialogForm"].clearValidate("houseId");
    },
    /** 查询楼栋列表 */
    handleQueryBuildList(code) {
      this.buildOptions = [];
      this.houseOptions = [];
      code != undefined &&
        service.requestCodeByBuild(code, data => {
          this.buildOptions = data.list;
        });
    },
    /** 查询户列表 */
    handleQueryHouseList(param) {
      this.houseOptions = [];
      if (param.buildingId != "") {
        service.requestBuildByHouse(param, data => {
          this.houseOptions = data.list;
        });
      }
    },
    submitForm() {
      this.$refs["dialogForm"].validate(valid => {
        if (valid) {
          let param = _.cloneDeep(this.dialogForm);
          param.dicMap && delete param.dicMap;
          param.approvalDate = util.time.formatDate(
            param.approvalDate,
            "YYYY-MM-DD"
          );
          if (this.isAdd == true) {
            // 添加
            service.requestSaveGroupSocial(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "新社会组织添加成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          } else if (this.isAdd == false) {
            // 修改
            service.requestUpdateGroupSocial(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "新社会组织修改成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm() {
      this.$refs["dialogForm"].resetFields();
      this.$refs["vCheckedHouse"].resetForm();
      this.$data.dialogVisible = false;
      this.isAdd = true;
      this.activeName = "first";
      this.dialogForm = {
        status: 1,
        isHaveConditions: 0,
        isHaveCommunist: 0,
        isHaveUnion: 0,
        isHaveYleague: 0,
        isHaveWomensorg: 0,
        isHaveAbroad: 0,
        houseId: "",
        districtCode: "",
        officeAddress: "",
        leaderIdcode: "111"
      };
    }
  }
};
</script>
<style>
.add-dialog .el-date-editor.el-input {
  width: 100%;
}

.add-dialog .el-select {
  width: 100%;
}

.add-dialog .el-cascader {
  width: 100%;
}

.add-dialog .check-house {
    width: 100%;
}
</style>